  background-color: #f5f5f5;
  border-radius: 1rem;
  border: solid 2px #757575;
  text-align: center;
  font-family: "Quicksand", sans-serif;
  font-size: 1.8em;
  font-style: normal;
  font-weight: 800;
  line-height: 105%;
  color: #ff5a00;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}
.reverse_link:hover {
  background-color: #650e70;
  color: #f5f5f5;
}
.reverse_link.active {
  background-color: #ff5a00;
  color: #650e70;
}

.block_for_selected_category {
  display: flex;
  flex-basis: 100%;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-evenly;
  gap: 2rem;
  margin: 2rem auto;
}

.nav_categories {
  display: flex;
  gap: 4rem;
  width: 100%;
  justify-content: space-between;
}

.categories_basic {
  display: flex;
  gap: 1rem;
  justify-content: space-around;
  width: 100%;
  flex-wrap: wrap;
}

.categories_block {
  display: flex;
  gap: 0.8rem;
  align-items: center;
  justify-content: space-between;
}

.products_container {
  width: 35.5%;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 4rem;
  gap: 0.5rem;
}
.products_container:hover {
  background-color: #ff8e08;
}

.img_cards_view {
  display: block;
  width: 120px;
  height: 180px;
  border-radius: 1rem;
}

/* стили для блока DISCOUNTS */
/* стили для блока ABOUT */
/* стили для блока BASKET */
/* стили для блока USER */
.amain-box {
  display: flex;
  flex-direction: column;
  background-color: #cbcbcb;
  align-items: center;
  justify-content: center;
}

.amain-wrap {
  display: flex;
  width: 97%;
  padding: 20px;
  margin: 20px;
  background-color: #f5f5f5;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
}

.amain-wrap h1 {
  align-items: center;
  font-size: 3rem;
}

.atitle {
  margin-bottom: 20px;
}

.auserlogo {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_9___});
  display: block;
  width: 10rem;
  height: 10rem;
  background-size: contain;
  background-repeat: no-repeat;
}

.aregistration {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.aregistration__error {
  font-family: "Quicksand", sans-serif;
  font-size: 1.2rem;
  font-style: normal;
  font-weight: 400;
  line-height: 105%;
  color: #e9a4a2;
}

.alabel_basic {
  display: flex;
  gap: 1rem;
  justify-content: center;
  align-items: center;
}

.ainput_basic {
  width: 21.4rem;
  height: 3.3rem;
  border: #757575 1px solid;
  box-shadow: 3px 3px 5px -2px rgba(0, 0, 0, 0.75);
  padding-left: 0.5rem;
  padding-bottom: 0.5rem;
  cursor: pointer;
  font-family: "Quicksand", sans-serif;
  font-size: 1.5rem;
  font-style: normal;
  font-weight: 600;
  line-height: 105%;
  color: #422779;
}
.ainput_basic::placeholder {
  font-family: "Quicksand", sans-serif;
  font-size: 1.2rem;
  font-style: italic;
  font-weight: 400;
  line-height: 105%;
  color: #757575;
  padding: 0.2rem;
}
.ainput_basic:focus {
  background-color: rgba(255, 144, 8, 0.5764705882);
  color: #ff5a00;
  border-color: #ff5a00;
  outline: 0;
}
.ainput_basic:invalid {
  border: 2px solid #e9a4a2;
  color: #303030;
  background-color: #ede5e5;
}
.ainput_basic:valid {
  border: 2px solid rgba(15, 241, 46, 0.2705882353);
  color: #ff5a00;
  background-color: rgba(255, 233, 8, 0.3098039216);
}

.asvg_calendar {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
  display: block;
  width: 2rem;
  height: 2rem;
  background-size: contain;
}

.asvg_map_pin {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
  display: block;
  width: 2rem;
  height: 2rem;
  background-size: contain;
}

.aaddress_block {
  border: #757575 2px solid;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-between;
  gap: 0.5rem;
  margin-bottom: 20px;
}

.aaddress_span {
  font-family: "Quicksand", sans-serif;
  font-size: 1rem;
  font-style: normal;
  font-weight: 600;
  line-height: 105%;
  color: #ff5a00;
}

.acheckbox_span {
  font-family: "Quicksand", sans-serif;
  font-size: 1.5rem;
  font-style: normal;
  font-weight: 600;
  line-height: 105%;
  color: #422779;
}

.aselect_block {
  display: flex;
  gap: 1rem;
  align-items: center;
  justify-content: center;
}

.aselect_basic {
  width: 10.5rem;
  height: 3rem;
  font-family: "Quicksand", sans-serif;
  font-size: 1.5rem;
  font-style: normal;
  font-weight: 600;
  line-height: 105%;
  color: #422779;
  border: #757575 2px solid;
  border-radius: 0.6rem;
  cursor: pointer;
}
.aselect_basic:focus {
  border-color: #ff5a00;
  outline: 0;
}
.aselect_basic option {
  font-family: "Quicksand", sans-serif;
  font-size: 1.5rem;
  font-style: normal;
  font-weight: 600;
  line-height: 105%;
  color: #422779;
  background-color: hsla(33, 100%, 52%, 0.576);
  color: #ff5a00;
}

.aaddress_title {
  font-family: "Quicksand", sans-serif;
  font-size: 2rem;
  font-style: normal;
  font-weight: 500;
  line-height: 105%;
  color: #ff5a00;
  text-align: center;
  border-bottom: 1px solid #303030;
  width: 20rem;
  display: none;
}

.abutton_registration {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 190px;
  background-color: #ff8e08;
  height: 30px;
  font-size: 1.5rem;
  color: #f5f5f5;
  border: none;
  border-radius: 6px;
  margin-bottom: 10px;
  cursor: pointer;
}
.abutton_registration:hover {
  background-color: #650e70;
  color: #f5f5f5;
}
.abutton_registration:disabled {
  background-color: darkgrey;
  cursor: default;
}

.ashipping_address {
  display: none;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-between;
  gap: 0.5rem;
}

.abilling_address {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-between;
  gap: 0.5rem;
}

.alink_to_loginPages {
  font-family: "Open Sans", sans-serif;
  font-weight: 500;
  color: #422779;
  font-size: 1.9rem;
  text-align: center;
  cursor: pointer;
  margin-top: 2rem;
}
.alink_to_loginPages:hover {
  background-color: #650e70;
  color: #f5f5f5;
}

.abasic_box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
  gap: 1.2rem;
}

.ashow-pass-text {
  display: flex;
  gap: 0.3rem;
  margin-right: 17rem;
}

.abasic_box button:hover {
  cursor: pointer;
  color: #ff8e08;
