n}\n\nul,\nol,\nli {\n  list-style: none;\n}\n\na {\n  text-decoration: none;\n  color: #422779;\n}\n\nhtml {\n  font-size: 10px;\n  font-family: \"Quicksand\", sans-serif;\n}\n@media (max-width: 668px) {\n  html {\n    font-size: 9px;\n  }\n}\n@media (max-width: 518px) {\n  html {\n    font-size: 6px;\n  }\n}\n\nbody {\n  background-color: #f5b668;\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n}\n\nbody > div:first-of-type {\n  width: 90%;\n  margin: 0 auto;\n}\n\n.title {\n  font-family: \"Open Sans\", sans-serif;\n  font-size: 1.5rem;\n  font-style: italic;\n  font-weight: 500;\n  line-height: 105%;\n  color: #303030;\n  padding: 1rem 0rem;\n}\n\n.basic-text {\n  font-family: \"Quicksand\", sans-serif;\n  font-size: 6.4rem;\n  font-style: normal;\n  font-weight: 600;\n  line-height: 105%;\n  color: #303030;\n}\n\n.text-register {\n  font-family: \"Quicksand\", sans-serif;\n  font-weight: 700;\n  font-size: 1.1rem;\n  margin-bottom: 4%;\n}\n\n/* стилизация глобального контейнера страницы */\n.main-box {\n  display: flex;\n  flex-direction: column;\n}\n\n.main-wrap {\n  display: flex;\n  flex-direction: column;\n  background-color: #f5f5f5;\n  align-items: center;\n  justify-content: space-evenly;\n  flex-grow: 1;\n  border: solid 2rem #cbcbcb;\n  flex-basis: 80%;\n}\n\n.main-title {\n  align-items: center;\n  font-size: 3rem;\n}\n\n.button-basic {\n  display: block;\n  width: 12rem;\n  height: 4.4rem;\n  background-color: #f5f5f5;\n  border-radius: 1rem;\n  border: solid 2px #757575;\n  text-align: center;\n  font-family: \"Quicksand\", sans-serif;\n  font-size: 1.8em;\n  font-style: normal;\n  font-weight: 800;\n  line-height: 105%;\n  color: #ff5a00;\n  cursor: pointer;\n}\n.button-basic:hover {\n  background-color: #650e70;\n  color: #f5f5f5;\n}\n.button-basic.active {\n  background-color: #ff5a00;\n  color: #650e70;\n}\n\n.header {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  gap: 2